export default {
	name: "jshopcoupon",
	props: {
		jdata:{
			// type: Array,
			required: true,
		}
	},
	computed: {
		count() {  
			return (this.jdata.params.list.length > 0)
		}
	},
	methods: {
		// 用户领取优惠券
		receiveCoupon(couponId) {
			let jdata = {
				promotion_id: couponId
			}
			this.$api.getCoupon(jdata, res => {
				if (res.status) {
					this.$common.successToShow(res.msg)
				} else {
					this.$common.errorToShow(res.msg)
				}
			})
		},
	}
}	
